const axios = require("axios");
const EncacapForm = require("../utils/form");
const { API_GATEWAY } = require("../configs/configs");
const prepare = require("../utils/prepare");

prepare(
    () => {
        window.localStorage.removeItem("user");
        window.localStorage.removeItem("tokens");

        const loginForm = new EncacapForm(".form");
        const loginButton = loginForm.querySelector("button[type=submit]");

        loginForm.validate({
            email: [
                { role: "required", message: "a ch Email khng c php  trng" },
                { role: "email", message: "a ch Email khng hp l" },
            ],
            password: [
                { role: "required", message: "Mt khu khng c php  trng" },
                { role: "min(6)", message: "Mt khu phi c t nht 6 k t" },
            ],
        });

        loginForm.onsubmit = async (event, data) => {
            event.preventDefault();
            loginButton.loading.show();
            try {
                const response = await axios.post(`${API_GATEWAY}/auth/login`, {
                    email: data.email.value,
                    password: data.password.value,
                });
                const { user, tokens } = response.data;
                window.localStorage.setItem("user", JSON.stringify(user));
                window.localStorage.setItem("tokens", JSON.stringify(tokens));
                window.location.href = "/";
            } catch (error) {
                const { response } = error;
                if (response) {
                    const { status } = response;
                    if (status === 401) {
                        loginForm.showError("a ch Email hoc mt khu khng chnh xc!");
                        loginButton.loading.hide();
                        loginForm.enable();
                    }
                } else {
                    loginForm.showError(" c li xy ra, vui lng th li sau!", error);
                }
            }
        };
    },
    {
        isVerify: false,
    }
);
